/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
	font-family: "Montserrat", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	font-family: BioRhyme;
}

h1 {
	margin: 0;
	font-size: 2.5rem;
	padding: 2rem 0 0;
	text-align: center;
}

h2 {
	font-size: 1.8rem;
	text-align: center;
}

h3 {
	font-size: 1.5rem;
	text-align: center;
	margin-top: 2rem auto 3rem;
}

@media screen and (min-width: 50em) {
	h1 {
		font-size: 3rem;
		margin-top: 4rem;
	}
}
p {
	margin-bottom: 1.5em;
	font-size: 1.1rem;
	line-height: 1.8;
	padding: 1rem;
	margin: 0 auto;
	max-width: 40rem;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background-color: #fdfbe8;
	color: #575757;
}
body .site-info {
	display: none;
}
@media screen and (min-width: 50em) {
	body .site-info {
		display: inline-block;
	}
}

.site {
	margin: 0 auto;
	min-height: 100vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	height: 100vh;
}

main {
	background-color: #fff;
	flex: 1 1 auto;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex-grow: 1;
}

@media screen and (min-width: 90em) {
	.site {
		min-height: unset;
		height: unset;
	}
}
hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	margin: 0 0 2rem;
	padding: 0 1rem;
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: center;
	gap: 1rem;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

li {
	color: #888888;
	border: 2px solid #888888;
	border-radius: 5px;
	padding: 0 0.5rem;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
	display: block;
	border-radius: 10px;
	border: 1px solid #888888;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #575757;
	text-decoration: none;
}
a:visited {
	color: #575757;
}
a:hover, a:focus, a:active {
	color: #575757;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

.site-info a {
	text-decoration: none;
	color: #575757;
}

.menu {
	list-style: none;
	display: flex;
	flex-direction: row;
	gap: 0.5rem;
}

.site-header a:hover,
.home a:hover,
.contact-social-icons a:hover {
	transition: all 0.2s ease-in-out;
	transform: scale(1.2);
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}
.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: -999em;
	z-index: 99999;
}
.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}
.main-navigation ul ul li:hover > ul, .main-navigation ul ul li.focus > ul {
	display: block;
	left: auto;
}
.main-navigation ul ul a {
	width: 200px;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}
.main-navigation li {
	position: relative;
}
.main-navigation a {
	display: block;
	text-decoration: none;
}
.menu-toggle,
.main-navigation.toggled ul {
	display: none;
}

.menu-main-menu-container {
	text-align: center;
}

.main-navigation ul {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 2rem;
}

.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
	margin: 0 1rem;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}
.comment-navigation .nav-title,
.posts-navigation .nav-title,
.post-navigation .nav-title {
	border-bottom: 2px solid #ffd72f;
}

body {
	position: relative;
}

.site-header {
	background-color: #fff;
	z-index: 1;
	padding: 1rem;
	position: fixed;
	bottom: 0;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
.site-header li {
	border: none;
}
.site-header a {
	color: #ee4926;
	font-weight: 800;
	font-family: BioRhyme;
	font-size: 1.1rem;
}

.menu svg {
	fill: #ee4926;
}

@media screen and (min-width: 50em) {
	.site {
		display: grid;
		grid-template-columns: 10rem auto;
	}
	.site-header {
		grid-column: 1/2;
		grid-row: 1/-1;
		background-color: #fff;
		position: static;
		border-top: 0;
	}
	.menu-main-menu-container {
		position: fixed;
		top: 0;
		left: 2.5rem;
		height: 100%;
		display: flex;
	}
	.menu-main-menu-container ul {
		flex-direction: column;
	}
	.menu-main-menu-container ul li:first-child {
		margin-left: 1rem;
	}
	.site-footer {
		grid-column: 1/-1;
		grid-row: 2/3;
	}
}
@media screen and (min-width: 75em) {
	.menu-main-menu-container {
		left: 5rem;
	}
}
svg {
	height: 2rem;
	width: 2rem;
	margin-right: 1rem;
	vertical-align: middle;
}

@media screen and (min-width: 50em) {
	.site-footer {
		background-color: #ee4926;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 1rem;
	}
	.site-info {
		text-align: center;
		margin-left: 9rem;
	}
	.site-info a {
		color: #fff;
		font-size: 0.8rem;
	}
}
/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.post,
.page {
	margin: 0;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em;
}
.widget select {
	max-width: 100%;
}

/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
	grid-template-columns: repeat(6, 1fr);
}

.gallery-columns-7 {
	grid-template-columns: repeat(7, 1fr);
}

.gallery-columns-8 {
	grid-template-columns: repeat(8, 1fr);
}

.gallery-columns-9 {
	grid-template-columns: repeat(9, 1fr);
}

.gallery-caption {
	display: block;
}

/* Homepage
--------------------------------------------- */
.home .entry-content {
	margin: 0;
}
.home h1 {
	background-color: #ee4926;
	color: #fff;
	text-align: center;
	padding: 8rem 1rem;
}
.home p {
	height: 5rem;
	font-weight: 500;
	text-align: center;
	margin: 3rem auto;
}
.home .wp-block-button__link {
	background-color: #fdfbe8;
	color: #ee4926;
	font-weight: 600;
	border: 2px solid #ee4926;
	padding: 0.5rem;
	font-size: 1rem;
	margin-bottom: 3rem;
	moz-transition: all 1s ease-in-out;
	-o-transition: all 1s ease-in-out;
	-webkit-transition: all 1s ease-in-out;
	transition: all 1s ease-in-out;
	box-shadow: 0 2px 15px 0 #ffd72f;
}
.home .wp-block-button__link:hover,
.home .wp-block-button__link:focus {
	transform: scale(1);
	color: #ee4926;
	moz-transition: all 0.8s ease-in-out;
	-o-transition: all 0.8s ease-in-out;
	-webkit-transition: all 0.8s ease-in-out;
	transition: all 0.8s ease-in-out;
	color: #fff;
	background: linear-gradient(90deg, #ee4926, #ffd72f);
}
.home .wp-block-buttons {
	display: flex;
	justify-content: center;
}
.home .contact-wrapper {
	flex-direction: row;
	border: none;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	gap: 1rem;
}
.home .contact-wrapper svg {
	fill: #ffd72f;
	height: 2.5rem;
	width: 2.5rem;
}
@media screen and (min-width: 50em) {
	.home .entry-content {
		display: grid;
		grid-template-columns: 1fr 1fr;
	}
	.home .home-title {
		background-color: #ffd72f;
		background-color: pink;
	}
	.home .home-title,
.home .home-content {
		height: 100vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.home .home-content p {
		font-size: 1.4rem;
	}
	.home .contact-wrapper {
		margin: 0;
	}
	.home .site-footer {
		display: none;
	}
}
@media screen and (min-width: 75em) {
	.home .site-main {
		padding-left: 4rem;
	}
}

/* Homepage
--------------------------------------------- */
.post-type-archive-portfolio-work .works {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.post-type-archive-portfolio-work .work {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
}
.post-type-archive-portfolio-work .work:nth-child(even) {
	background-color: #fdfbe8;
}
.post-type-archive-portfolio-work .work-img {
	padding: 0 1rem;
}
.post-type-archive-portfolio-work .work-content {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
